import { addToLocalStoragePatterns } from '../utils/LocalStorage'

const initialstate = []



export const followedPatternsReducer = (state = initialstate, action) => {

  switch (action.type) {
  case 'ADD_PATTERN':
    return state.concat(action.data)
  case 'SET_PATTERNS':
    return action.data

  case 'INIT':
    return initialstate
    
  
  default:
    return state
  }
}

export const setPatterns = patterns => {

  return {
    type: 'SET_PATTERNS',
    data: patterns
  }

}


export const addPattern = pattern => {

  const patternId = idFromPattern(pattern)

  // Handle local storage
  addToLocalStoragePatterns( patternToString(pattern)  )

  return {
    type: 'ADD_PATTERN',
    data: patternId
  }
}


export const addPatterns = patterns => {


  // Handle local storage
  patterns.forEach(pattern =>  addToLocalStoragePatterns(  patternToString(pattern) ) )

  return {
    type: 'SET_PATTERNS',
    data: patterns.map(idFromPattern)
  }
}


const idFromPattern = pattern =>  ({ stopId: pattern.stop.key, patternId: pattern.pattern.code })
const patternToString = pattern => pattern.stop.key + '|' + pattern.pattern.code