import React, { useEffect } from 'react'
import { connect } from 'react-redux'


import { getTimetables }  from './reducers/timetableReducer'
import Departures     from './components/Departures'
import ChooseBus from './components/ChooseBus'
import { getLocalStoragePatterns } from './utils/LocalStorage'
import { setPatterns } from './reducers/followedPatternsReducer'
import { Container } from 'semantic-ui-react'

const App = ({ setPatterns, followedPatterns }) => {


  useEffect(() => {
    // Import from local storage
    setPatterns(getLocalStoragePatterns().filter(s => s.patternId))  

  },[])



  return(

    <Container>
       {
          followedPatterns.length > 0 ? 
            <Departures/>
            :
            <ChooseBus />
      
        } 
    </Container>

  )

}




const mapStateToProps = (state) =>  ({ ...state })
export default connect(mapStateToProps, {   getTimetables, setPatterns })(App)