import React, { useState } from 'react'
import Search from './Search'
import { connect } from 'react-redux'

import { List, Button, Container, Icon, Header } from 'semantic-ui-react'
import uuid from 'uuid'
import {  addPatterns } from '../reducers/followedPatternsReducer'


const ChooseBus = ({ addPatterns, followedPatterns }) => {

    const [selectedStop,setSelectedStop] = useState(undefined)
    const [selectedPatterns,setSelectedPatterns] = useState([] )

    console.log('selectedPatterns',selectedPatterns)

    return(
        <Container>
            {
                selectedStop ?
                    <div>
                        <Header as='h2'>{ selectedStop.name }</Header>

                        <List divided verticalAlign='middle'>
                                {
                                    selectedStop.patterns.map(pattern => {

                                        const icon = pattern.route.mode === 'RAIL' ? 'train' : pattern.route.mode.toLowerCase()

                                        // Dont add if already followed
                                        if (
                                            followedPatterns.find(p => p.patternId === pattern.code)
                                            ||
                                            selectedPatterns.find(s => s.pattern.code === pattern.code)
                                            ) {
                                            return('')
                                        }



                                        return(
                                            <List.Item key={ uuid() }>
                                                <List.Content floated='right'>
                                                    <Button
                                                        positive
                                                        onClick={ () => setSelectedPatterns(  selectedPatterns.concat( { stop: selectedStop, pattern }) ) }
                                                    >
                                                        <Icon name={icon} style={{ fontSize: '150%', color: 'white' }}></Icon>
                                                        Seuraa
                                                    </Button>
                                                </List.Content>
                                                <List.Content>
                                                    <span style={{ marginRight: '2%' }}>Suunta: { pattern.headsign }</span>
                                                    <span style={{ fontSize: '150%'}}>{pattern.route.shortName}</span>
                                                    
                                                </List.Content>
                                            </List.Item>
                                        )
                                    })
                                }
                            </List>
                        </div>
                    :
                    <Search setSelectedStop={setSelectedStop} />
            }
            <Button positive onClick={ () => addPatterns(selectedPatterns) } >Valmista</Button>
            

        </Container>
    )
}



const mapStateToProps = (state) =>  ({ ...state })
export default connect(mapStateToProps, {   addPatterns })(ChooseBus)